import * as InlineEditor from "@ckeditor/ckeditor5-build-inline";

import { Component, EventEmitter, OnInit } from "@angular/core";

// import { ChangeEvent } from '@ckeditor/ckeditor5-angular/ckeditor.component';
// import {ElectronService} from 'ngx-electron';
import { HttpClient } from "@angular/common/http";
import { _ } from "underscore";

@Component({
  selector: "app-university",
  templateUrl: "./university.component.html",
  styleUrls: ["./university.component.scss"]
})
export class UniversityComponent implements OnInit {
  public Editor = InlineEditor;

  public active: boolean = true;
  public contentType: any;
  public model = {
    data: null,
    blogList: [],
    currentBlog: null,
    imgURL: null,
    blogData: {
      title: null,
      tags: []
    }
  };
  public blogModel = {
    data: null,
    blogList: [],
    currentBlog: null,
    imgURL: null,
    blogData: {
      title: null,
      tags: []
    }
  };

  constructor(private http: HttpClient) {
    this.http
      .get("http://localhost:5000/fetch-list")
      .toPromise()
      .then((res: any) => {
        this.model.blogList = res.universityList;
        this.blogModel.blogList = res.blogList;
      });
  }

  public editTitle() {
    this.active = !this.active;
  }

  // overwriting particular blog file after button click

  public uploadPost(): void {
    if (this.contentType === "university") {
      this.http
        .post("http://localhost:5000/upload", this.model)
        .toPromise()
        .then((res: any) => {
          this.http
            .get("http://localhost:5000/fetch-list")
            .toPromise()
            .then((res: any) => {
              this.model.blogList = res.universityList;
              this.blogModel.blogList = res.blogList;
            });
        });
    } else {
      this.http
        .post("http://localhost:5000/upload-blog", this.model)
        .toPromise()
        .then((res: any) => {
          this.http
            .get("http://localhost:5000/fetch-list")
            .toPromise()
            .then((res: any) => {
              this.model.blogList = res.universityList;
              this.blogModel.blogList = res.blogList;
            });
        });
    }
  }

  // display particular blog data

  showUniversity(list, type) {
    this.contentType = type;
    this.model.currentBlog = list;
    this.http
      .post("http://localhost:5000/fetch-university", this.model)
      .toPromise()
      .then((res: any) => {
        this.model.data = res.data;
        this.model.blogData = res.blogData;
        this.model.blogList = res.blogList;
        this.model.imgURL = res.imgURL;
      });
  }

  showBlog(list, type) {
    this.contentType = type;
    this.model.currentBlog = list;
    this.http
      .post("http://localhost:5000/fetch-blogs", this.model)
      .toPromise()
      .then((res: any) => {
        this.model.data = res.data;
        this.model.blogData = res.blogData;
        this.blogModel.blogList = res.blogList;
        this.model.imgURL = res.imgURL;
      });
  }

  delete(list, type) {
    console.log(list, type);
    this.http
      .post("http://localhost:5000/delete", { title: list, type })
      .toPromise()
      .then(res => {
        console.log(res);
        this.http
          .get("http://localhost:5000/fetch-list")
          .toPromise()
          .then((res: any) => {
            this.model.blogList = res.universityList;
            this.blogModel.blogList = res.blogList;
          });
      });
  }

  public createBlog(blogName, type) {
    this.http
      .post("http://localhost:5000/create", { title: blogName, type })
      .toPromise()
      .then(res => {
        console.log(res);
        this.http
          .get("http://localhost:5000/fetch-list")
          .toPromise()
          .then((res: any) => {
            this.model.blogList = res.universityList;
            this.blogModel.blogList = res.blogList;
          });
      });
  }

  public imagePath;
  // imgURL: any;
  public message: string;

  preview(files) {
    if (files.length === 0) return;

    var mimeType = files[0].type;
    if (mimeType.match(/image\/*/) == null) {
      this.message = "Only images are supported.";
      return;
    }

    var reader = new FileReader();
    this.imagePath = files;
    reader.readAsDataURL(files[0]);
    reader.onload = _event => {
      this.model.imgURL = reader.result;
    };
  }

  launchSnackBar() {
    // Get the snackbar DIV
    var x = document.getElementById("snackbar");

    // Add the "show" class to DIV
    x.className = "show";

    // After 3 seconds, remove the show class from DIV
    setTimeout(function() {
      x.className = x.className.replace("show", "");
    }, 3000);
  }

  ngOnInit() {}
}
